}

.icon-close {
	@extend .icon;
	@extend %fa-close;
}

.icon-remove {
	@extend .icon;
	@extend %fa-close;
}

.icon-container-info {
	@extend .icon-container;
	background-color: $sc-color-info;
}

.icon-info {
	@extend .icon;
	@extend %fa-info-circle;
}
.icon-question {
	@extend .icon;
	@extend %fa-question;
}
