/* Edited for Threads Theme */

.product-views-option-color{
	margin-bottom:$sc-margin-lv2;

	[data-validation-error="block"]{
  		display:table;
  		margin-top:$sc-margin-lv1;
  	}
}

.product-views-option-color-label{
    font-size: $sc-font-size-xl;
    font-weight: $sc-font-weight-light;
    margin-bottom: 0;
}

.product-views-option-color-container{
	@extend .color-picker-lg;
	clear:both;
}

.product-views-option-color-container-small{
	@extend .color-picker-sm;
	clear:both;
}

.product-views-option-color-picker {
	@extend .picker-color;
	@extend .picker-color-sm;
}
.product-views-option-color-picker-label {
	@extend .picker-color-label;
}
.product-views-option-color-picker-input {
	@extend .picker-color-input;
}
.product-views-option-color-picker-box {
	@extend .picker-color-box;
	width: 30px !important;
    height: 30px !important;
	border-radius:50%;
	opacity: 0.5;
}
.product-views-option-color-picker-box.active {
	@extend .picker-color-box-active;
	opacity: 1 !important;
}
.product-views-option-color-picker-box.muted {
	@extend .picker-color-box-disabled;
}
.product-views-option-color-picker-box-white-border {
	@extend .picker-color-box-white-border;
}
.product-views-option-color-picker-box-img {
	@extend .picker-color-box-img;
}
.product-views-option-color-picker-box-img.muted {
	@extend .picker-color-box-img-disabled;
}
.product-views-option-color-picker-box-img.active {
	@extend .picker-color-box-img-active;
}

.product-views-option-color-label-required{
	@extend .input-required;
}
.product-views-option-color-picker-anchor {
	border:0px;
}

.product-views-option-color-value-container {
    color: $sc-color-theme-500;
}

.product-views-option-color-value {
    // @extend .sc-value;
    text-transform: capitalize;
}

.product-views-option-color-label-header {
	display:block;
    margin-bottom: $sc-margin-lv2;
    @extend %theme-label;
}
