$sc-color-body-background: white; /* editable({"type": "color", "label": "Body background"}) */

html,
body {
	font-family: $sc-font-family;
	background-color: $sc-color-body-background;
	font-size: $sc-font-size-base;
	min-height:100%;
	height: auto;

	text-transform: lowercase;
}

body,
html,
div,
article,
aside,
section,
figure,
nav,
h1, h2, h3, h4, h5, h6,
p, pre, ul, ol, dl, address, a, input, textarea, button, fieldset {
	margin: 0;
	padding: 0;
	border: 0;
	text-transform:lowercase;
}

address{
	font-style:normal;
}

ul, li {
	list-style: none;
}

img {
	border: 0;
	max-width: 100%;
	height: auto;
}

a {
	color: $sc-color-link;
	text-decoration: none;
	cursor:pointer;
    cursor:hand;
    transition: all 300ms ease-in-out;
}

a:hover, a:focus {
	// color: $sc-color-link-hover;
	color:$sc-mc-link-hover-color;
	text-decoration: none;
}

a:active {
	// color: $sc-color-link-active;
	color:$sc-mc-link-active-color;
}

[data-toggle="dropdown"]:hover, [data-toggle="dropdown"]:focus{
	color: $sc-color-copy;
}

.open > [data-toggle="dropdown"]{
	color: $sc-color-copy-dark;
}

hr{
	height: 1.5px;
	border: 0;
	background-color: $sc-color-theme-border-light;
}

* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}

::-webkit-scrollbar{
	width:10px;
	height:10px;
}
::-webkit-scrollbar-thumb{
	background:0 0;
	background-color:transparentize($sc-neutral-shade-500, 1-0.25);
	border:2px solid transparent;
	border-radius:10px;
	background-clip:padding-box;
}
::-webkit-scrollbar-thumb:hover{
	background-color:transparentize($sc-neutral-shade-500, 1-0.5);
}
::-webkit-scrollbar-track{
	background-color:transparentize($sc-neutral-shade-500, 1-0.05);
}
