.product-views-option-dropdown{
	margin-bottom:$sc-margin-lv4;

	[data-validation-error="block"] {
  		display:table;
  		margin-top:$sc-margin-lv1;
  	}
}

.product-views-option-dropdown-label{
	@extend .product-views-option-color-label;
	margin-bottom:$sc-margin-lv2;
	display:block;
}

.product-views-option-dropdown-select{
	@extend .input-medium;
}
.product-views-option-dropdown-label-required{
	@extend .input-required;
}